
//import { GET_RECIPES } from '../actions/index'

const initialState = {
    recipes: [],
    allRecipes: []
}

function rootReducer (state = initialState, action){
    switch(action.type){
        case GET_RECIPES:
            return{
                ...state,
                recipes: action.payload,
                allRecipes: action.payload
            };
            defaul:
            return{
                ...state
            }
    }

}

export default rootReducer